#container {
  width: 400px;
  background-color: aliceblue;
  padding: 20px;
}
input {
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.list {
  margin: 15px 0px;
}
.list > li {
  background-color: antiquewhite;
  padding: 5px;
  margin-bottom: 2px;
  display: flex;
  justify-content: space-between;
}
.btn {
  display: flex;
  justify-content: flex-end;
  padding: 5px 0;
}
.btn > button {
  padding: 5px;
  width: 60px;
  background-color: orange;
  border: none;
  outline: none;
  cursor: pointer;
}
.add-input {
  margin: 10px 0;
}
